  max-width: 800px;
}

.no-margin {
  margin: 0;
}

.acciones-tab {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.cont-borde {
  background-color: #fefefe;
  padding: 1rem;
  margin: 1rem auto;
  border-radius: 1rem;
}

.an-left {
  position: relative;
  -webkit-animation-name: animateleft;
  -webkit-animation-duration: 0.9s;
  animation-name: animateleft;
  animation-duration: 0.9s;
}

.an-right {
  position: relative;
  -webkit-animation-name: animateright;
  -webkit-animation-duration: 0.5s;
  animation-name: animateright;
  animation-duration: 0.5s;
}

@-webkit-keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@-webkit-keyframes animateright {
  from {
    right: -12px;
    opacity: 0;
  }
  to {
    right: 0;
    opacity: 1;
  }
}
@keyframes animateright {
  from {
    right: -12px;
    opacity: 0;
  }
  to {
    right: 0;
    opacity: 1;
  }
}
.fade-ef {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1s;
  animation-name: fade;
  animation-duration: 1s;
}

@-webkit-keyframes fade-ef {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-ef {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.contenedor-barra {
  position: relative;
  background-color: #0c407c;
