  padding: 0.5rem;
  font-size: 1.5rem;
  overflow: hidden;
  margin-right: 0.2rem;
  margin-left: 0.2rem;
}
.items a i {
  padding-right: 1rem;
  font-size: 2.5rem;
  justify-content: center;
}
.items a:hover {
  background-color: #082d57;
  color: #ffffff;
}
.items a.activo {
  background-color: #051c36;
  border-radius: 0.5rem;
}
.items a:last-of-type {
  margin-bottom: 0;
}
